<template>
  <div id="connectionStateDisplay" :class="[isOnline ? 'online' : 'offline']" @click="toggleState">
  </div>
</template>

<script>
  export default {
    data() {
      return {
        isOnline: false
      };
    },
    methods: {
      onOnline() {
        this.isOnline = true;
        this.$emit('changeBodyColor', ['#4caf50']);
        /*
         this.approveOnlineState().catch(() => {
         console.log('Could not connect.');
         this.onOffline();
         });
         */
      },
      onOffline() {
        this.isOnline = false;
        this.$emit('changeBodyColor', ['#f44336']);
      },
      toggleState() {
        // Testing the transition
        if (!this.isOnline) {
          this.onOnline();
        } else {
          this.onOffline();
        }
      },
      approveOnlineState() {
        return new Promise((resolve, reject) => {

          const xhr = new XMLHttpRequest();
          xhr.open(XMLHttpRequest.GET, 'http://google.com');

          xhr.onreadystatechange = () => {
            if (xhr.readyState === XMLHttpRequest.HEADERS_RECEIVED) {
              xhr.abort();
              resolve();
            }
          };

          xhr.onerror = (e) => {
            reject();
          };
          xhr.send();
        });
      },
      checkOnline() {
        return new Promise((resolve, reject) => {
          const img = new Image();
          img.onload = () => {
            resolve();
          };

          img.onerror = () => {
            reject();
          };

          img.src = 'http://via.placeholder.com/1x1?' + new Date().getTime();
        });
      }
    },
    mounted() {
      this.onOffline();

      setInterval(() => {
        this.checkOnline().then(() => {
          if (!this.isOnline)
            this.onOnline();
        }).catch(() => {
          if (this.isOnline)
            this.onOffline();
        });
      }, 1000);

      /*
       window.addEventListener('online', this.onOnline);
       window.addEventListener('offline', this.onOffline);
       */
    },
    beforeDestroy() {
      window.removeEventListener('online', this.onOnline);
      window.removeEventListener('offline', this.onOffline);
    }
  };
</script>

<style scoped>
  #connectionStateDisplay {
    width: calc(100% - 10px);
    height: calc(100% - 10px);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    transition: background-color 2s;

    /* For Electron */
    -webkit-app-region: drag;
  }

  .online {
    background: #4caf50;
  }

  .offline {
    background: #f44336;
  }
</style>



